body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/dutygenerator/static/media/pic1.ac0c8b27.jpg);
}

.App{
  background: #ffffff;
  margin: 20px auto;
  width: 90%;
  max-width: 700px;
  color: #eee;
}

.navbar{
  padding: 10px 20px;
  text-align: center;
  background: #2F3136;
}

.navbar h1{
  margin: 10px 0;
}

.book-list{
  margin: 20px;
}

.book-list ul{
  padding: 0;
  list-style-type: none;
}

.book-list li{
  background: #2F3136;
  border-radius: 4px;
  padding: 10px;
  cursor: pointer;
  margin: 10px 0;
}

/* .book-list .title:hover{
  opacity: 0.7;
  text-decoration: line-through;
} */

.book-list .title{
  font-weight: bold;
  color: #fff;
  font-size: 1.2em;
}

.book-list .author{
  font-size: 0.9em;
  color: #ddd;
}

.empty{
  margin: 20px;
  text-align: center;
}

form{
  padding: 20px;
}

input[type='text'],input[type='email'],input[type='password'],select,textarea{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background:transparent;
  color: #2F3136;
  border: 0;
}

input[name='Album']{
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  margin: 6px 0;
  background:transparent;
  color: #fff;
  border: 0;
}

input[type='submit']{
  margin: 10px auto;
  color: #ddd;
  cursor: pointer;
  background: #2F3136;
  border: 0;
  padding: 6px 20px;
  display: block;
}

button{
  margin: 10px auto;
  color: #ddd;
  cursor: pointer;
  background: #2F3136;
  border: 0;
  padding: 6px 20px;
  display: block;
}

